* {
  box-sizing: border-box;
  background-color: #f6eee9;
  margin: 0;
  padding: 0;
}

header {
  padding-top: 20px;
  padding-bottom: 20px;
  position: relative;
}

h1 {
  text-align: center;
  margin: 20px;
  padding: 10px;
  font-size: 40px;
}

.buttonContainer {
  justify-content: space-between;
  width: 70%;
  margin: auto;
  display: flex;
}

.page-btn {
  width: auto;
  padding: 10px 30px;
  font-size: 24px;
}

#myPage {
  background-color: #f7d9c4;
}

#members {
  background-color: #c6def1;
}

#forum1 {
  background-color: #dbcdf0;
}

#forum2 {
  background-color: #f2c6de;
}

#forum3 {
  background-color: #c9e4de;
}

.forum-form {
  justify-content: center;
  align-items: center;
  width: 70%;
  margin: 20px auto;
}

.forum-form input[type="text"] {
  border-radius: 5px;
  width: 50%;
  margin-bottom: 10px;
}

.forum-form textarea {
  border-radius: 5px;
  width: 100%;
  height: 3rem;
  margin-bottom: 10px;
}

.forum-form label {
  font-size: 20px;
}

.forum-form input[type="submit"] {
  border-radius: 5px;
  padding: 10px;
  font-size: 15px;
}

.comment-wrapper {
  background-color: #f9f9f9;
  border: 1px solid #000;
  border-radius: 5px;
  flex-direction: column;
  align-items: flex-start;
  width: 70%;
  margin: auto auto 15px;
  padding: 10px;
  display: flex;
  position: relative;
}

.comment-wrapper button {
  padding: 2px;
  position: absolute;
  top: 10px;
  right: 10px;
}

.comment-wrapper h2 {
  margin-top: 0;
  font-size: 18px;
}

.comment-wrapper p {
  font-size: 16px;
}

.forum-forum1-comments, .forum-forum1-comments h2, .forum-forum1-comments p {
  background-color: #dbcdf0;
}

.forum-forum2-comments, .forum-forum2-comments h2, .forum-forum2-comments p {
  background-color: #f2c6de;
}

.forum-forum3-comments, .forum-forum3-comments h2, .forum-forum3-comments p {
  background-color: #c9e4de;
}
